.navigation {
	position: absolute;
	display: flex;
	justify-content: space-around;
	align-items: center;
	height: 80px;
	width: 100%;
	z-index: 100;

	&_bars {
		display: none;
		justify-content: center;
		align-items: center;
		width: 50px;
		height: 50px;
		display: none;
		color: white;
		cursor: pointer;

		span {
			display: block;
			width: 35px;
			height: 5px;
			background-color: white;
			border-radius: 8px;

			&::after,
			&::before {
				content: '';
				display: block;
				width: 35px;
				height: 5px;
				background-color: white;
				border-radius: 8px;
				transform: translateY(5px);
			}

			&::before {
				transform: translateY(-10px);
			}
		}
	}

	&_logo {
		display: flex;
		justify-content: center;
		align-items: center;
		font-size: 35px;
		color: white;

		&_img {
			height: 45px;
			margin-right: 15px;
		}
	}

	&_item {
		position: relative;
		display: flex;
		justify-content: center;
		align-items: center;
		list-style: none;

		li {
			margin: 0 10px;

			a {
				color: white;
				text-decoration: none;

				img {
					height: 40px;
				}
			}
		}

		span {
			position: absolute;
			top: 0;
			right: 0;
			display: block;
			text-align: center;
			line-height: 25px;
			background-color: red;
			color: white;
			width: 25px;
			height: 25px;
			border-radius: 50px;
		}
	}

	&_basket {
		position: fixed;
		display: flex;
		flex-direction: column;
		right: 0;
		top: 0;
		width: 400px;
		background-color: rgb(255, 255, 255);
		height: 100vh;
		font-size: 25px;
		padding: 20px;
		transform: translateX(400px);
		transition: 1s;
		color: black;

		&_active {
			transform: translateX(0);
		}

		&_top {
			display: flex;
			justify-content: space-between;
			height: 50px;
			width: 100%;
		}

		&_items {
			display: flex;
			flex-direction: column;

			&_item {

				padding: 20px 0;
				h2 {
					padding: 5px 0;
					font-size: 20px;
				}
				&_ingredients{
					padding: 0 15px;
					color: #555;
					font-size: 15px;
				}
				img {
					width: 100px;
					height: 120px;
					border-radius: 8px;
				}

				&_wrapper {
					display: flex;
					padding: 20px 0;
				}
				&_wrapper2 {
					display: flex;
					flex-direction: row-reverse;

					select {
						border: none;
					}
				}

				&_price {
					font-size: 25px;
				}

				&::after {
					content: '';
					display: block;
					height: 1px;
					width: 100%;
					background-color: #999;
				}
			}
		}
	}
}

@media screen and (max-width: 768px) {
	.navigation {
		justify-content: space-between;
		padding: 0 20px;
		&_item {
			position: fixed;
			top: 0;
			right: 0;
			flex-direction: column;
			background: #888888; /* fallback for old browsers */
			width: 40vw;
			height: 100vh;
			transform: translatex(80vw);
			transition: 1s;

			li {
				padding: 20px;
				a {
					font-size: 25px;
					color: black;
				}

				&:hover {
					text-decoration: underline;
				}
			}

			&_active {
				transform: translatex(0);
			}
		}

		&_bars {
			display: flex;
			z-index: 1000;
		}
	}
}
