.products {
	display: flex;
	justify-content: space-around;
	align-items: center;
	
	&_item {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		width: 350px;
		height: 350px;
		overflow: hidden;

		&_photo {
			width: 250px;
			height: 200px;
			background-color: red;
            background-image: url('../small-cake-gd6f70e349_640.jpg');
            background-size: cover;
            background-position: center;
            border-radius: 8px;
		}
        &:nth-child(2) .products_item_photo {
            background-image: url('../balladyna-smaker-1-1.jpg');
        }
        &:nth-child(3) .products_item_photo {
            background-image: url('../pobrane.jpg');
        }

		h2 {
			padding-bottom: 25px;
			font-weight: 400;
		}

		button {
			display: flex;
			justify-content: center;
			align-items: center;
			margin: 20px;
			padding: 10px 25px;
			font-size: 15px;
			border-radius: 8px;
			border: none;
			color: white;
			background-color: #078ff2;
			cursor: pointer;
		}

        p {margin-top: 5px;
            font-size: 25px;
        }
	}
}

@media screen and (max-width: 768px) {
	.products {
		flex-direction: column;
	}
}